import React from 'react'
import { Link } from 'react-router-dom'
export default function Header() {
    return (
        <header style={headerStyle}>
            <h1>CENIMA ONE</h1>
            <Link style={linkStyle} to="/">Home</Link> | <Link style={linkStyle} to="/Movies">Movies</Link> | <Link style={linkStyle} to="/login">Login</Link>
        </header>
    )
}

const headerStyle={
    background: '#333',
    color: '#fff',
    textAlign: 'center'
}

const linkStyle = {
    color: '#fff',
    textDecoration: 'none'
}

